import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import ContactForm from "@screens/contactus/contactform";
import { useLocation } from "@reach/router";

const ContactUs = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Contact us", link: null },
  ];
  const { data } = props;
  const location = useLocation();
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  const validData = data?.allMetaJson?.nodes[0]

  return (
    <Layout db={db}>
      <Seo
        title="Contact us | Justtry Technologies"
        metaTitle="Contact us | Justtry Technologies"
        description="Reach out to us through our contact page for quick and efficient assistance. Whether you have questions, feedback, or partnership inquiries, our team is here to help. Connect with us today!"
        image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
        // noIndex={true}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={location.pathname.split("/")?.[1]}
      ></Seo>
      <BreadCrumb items={breadcrumbLinks} />
      <ContactForm db={db} />
    </Layout>
  );
};

