export default ContactUs;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allMetaJson(filter: {slug: {eq: "contact-us"}}) {
      nodes {
        metaTitle
        title
        slug
       description
      }
    }
  }
`;
